body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  overflow-x: hidden;
  color: #438496 !important;
}

p {
  font-size: 20px
}

p.small {
  font-size: 16px
}

a,
a.active,
a:active,
a:focus,
a:hover {
  outline: 0;
  color: #70b9b0
}

.navbar-light .navbar-nav .active>.nav-link,
.navbar-light a.navbar-brand,
.navbar-light .navbar-nav .nav-link{
  color: #ffffff !important;
  cursor: pointer;
}

.navbar-light a.navbar-brand:focus,
.navbar-light a.navbar-brand:hover,
.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover{
  color: #70b9b0;
  background-color: transparent
}

.navbar-light .navbar-nav .active>.nav-link:focus,
.navbar-light .navbar-nav .active>.nav-link:hover{
  color: #fff;
  background-color: #70b9b0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-transform: uppercase;
  font-family: Montserrat, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: 700
}

hr.star-light,
hr.star-primary {
  margin: 25px auto 30px;
  padding: 0;
  max-width: 250px;
  border: 0;
  border-top: 5px solid;
  text-align: center
}

hr.star-light:after,
hr.star-primary:after {
  content: "\f005";
  display: inline-block;
  position: relative;
  top: -.8em;
  padding: 0 .25em;
  font-family: FontAwesome;
  font-size: 2em
}

hr.star-light {
  border-color: #fff
}

hr.star-light:after {
  color: #fff;
  background-color: #70b9b0
}

hr.star-primary {
  border-color: #438496
}

hr.star-primary:after {
  color: #438496;
  background-color: #fff
}

.img-centered {
  margin: 0 auto
}

header {
  text-align: center;
  color: #fff;
  background: #70b9b0
}

header .container {
  padding-top: 100px;
  padding-bottom: 50px
}

header img {
  display: block;
  margin: 0 auto 20px
}

header .intro-text .name {
  display: block;
  text-transform: uppercase;
  font-family: Montserrat, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 2em;
  font-weight: 700
}

header .intro-text .skills {
  font-size: 1.25em;
  font-weight: 300
}

@media(min-width:768px) {
  header .container {
      padding-top: 200px;
      padding-bottom: 100px
  }
  header .intro-text .name {
      font-size: 4.75em
  }
  header .intro-text .skills {
      font-size: 1.75em
  }
}

@media(min-width:768px) {
  .fixed-top {
      padding: 25px 0;
      -webkit-transition: padding .3s;
      -o-transition: padding .3s;
      transition: padding .3s
  }
  .fixed-top .navbar-brand {
      font-size: 2em;
      -webkit-transition: all .3s;
      -o-transition: all .3s;
      transition: all .3s
  }

  .nav-link {
      padding-top: 15px;
      padding-bottom: 15px;
  }
}

.navbar {
  text-transform: uppercase;
  font-family: Montserrat, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: 700
}

.navbar a:focus {
  outline: 0
}

.navbar .navbar-nav {
  letter-spacing: 1px
}

.navbar .navbar-nav li a:focus {
  outline: 0
}

.navbar-light,
.navbar-inverse {
  border: 0
}

section {
  padding: 100px 0
}

section h2 {
  margin: 0;
  font-size: 3em
}

section.success {
  color: #fff;
  background: #70b9b0
}

section.success a,
section.success a.active,
section.success a:active,
section.success a:focus,
section.success a:hover {
  outline: 0;
  color: #2c3e50
}

@media(max-width:767px) {
  section {
      padding: 75px 0
  }
  section.first {
      padding-top: 75px
  }
}

#portfolio .portfolio-item {
  right: 0;
  margin: 0 0 15px
}

#portfolio .portfolio-item .portfolio-link {
  display: block;
  position: relative;
  margin: 0 auto;
  max-width: 400px
}

#portfolio .portfolio-item .portfolio-link .caption {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  background: rgba(112, 185, 176, .9);
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease
}

#portfolio .portfolio-item .portfolio-link .caption:hover {
  opacity: 1
}

#portfolio .portfolio-item .portfolio-link .caption .caption-content {
  position: absolute;
  top: 50%;
  width: 100%;
  height: 20px;
  margin-top: -12px;
  text-align: center;
  font-size: 20px;
  color: #fff
}

#portfolio .portfolio-item .portfolio-link .caption .caption-content i {
  margin-top: -12px
}

#portfolio .portfolio-item .portfolio-link .caption .caption-content h3,
#portfolio .portfolio-item .portfolio-link .caption .caption-content h4 {
  margin: 0
}

#portfolio * {
  z-index: 2
}

.navbar-light {
  background-color: #438496
}

.navbar-light .navbar-nav>.active>a,
.navbar-light .navbar-nav>.active>a:focus,
.navbar-light .navbar-nav>.active>a:hover {
  color: #fff;
  background-color: #70b9b0
}

.navbar-light .navbar-nav>li>a:focus,
.navbar-light .navbar-nav>li>a:hover {
  color: #70b9b0;
  background-color: transparent
}

.btn-success {
  color: #fff;
  background-color: #70b9b0;
  border-color: #70b9b0
}

.responsive-circle {
  font-size: 80px;
  font-weight: 700;
  margin: 0 auto 20px;
  width: 30%
}

.responsive-circle:after {
  border-radius: 50%;
  content: "";
  display: block;
  height: 0;
  margin-bottom: 20px;
  padding-bottom: 100%;
  width: 100%
}

.responsive-circle div {
  color: #fff;
  float: left;
  line-height: 1em;
  margin-top: -.5em;
  padding-top: 50%;
  text-align: center;
  width: 100%
}

.profile-img:after {
  background: url(./img/447.jpg) repeat scroll 0 0/cover;
  background-position-x: 45%
}

.nav>.page-scroll>a,
.navbar-header>a {
  cursor: pointer
}

.text-align {
  text-align: justify
}

.btn-success:hover {
  color: #fff;
  background-color: #70b9b0;
  border-color: #70b9b0
}

.btn-outline {
  margin-top: 15px;
  border: 2px solid #fff;
  font-size: 20px;
  color: #fff;
  background: 0 0;
  -webkit-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out
}

.btn-outline.active,
.btn-outline:active,
.btn-outline:focus,
.btn-outline:hover {
  border: 2px solid #fff;
  color: #18bc9c;
  background: #fff
}

.floating-label-form-group {
  position: relative;
  margin-bottom: 0;
  padding-bottom: .5em;
  border-bottom: 1px solid #eee
}

.floating-label-form-group input,
.floating-label-form-group textarea {
  z-index: 1;
  position: relative;
  padding-right: 0;
  padding-left: 0;
  border: 0;
  border-radius: 0;
  font-size: 1.5em;
  background: 0 0;
  -webkit-box-shadow: none!important;
  box-shadow: none!important;
  resize: none
}

.floating-label-form-group label {
  display: block;
  z-index: 0;
  position: relative;
  top: 2em;
  margin: 0;
  font-size: .85em;
  line-height: 1.764705882em;
  vertical-align: middle;
  vertical-align: baseline;
  opacity: 0;
  -webkit-transition: top .3s ease, opacity .3s ease;
  -o-transition: top .3s ease, opacity .3s ease;
  transition: top .3s ease, opacity .3s ease
}

.floating-label-form-group::not(:first-child) {
  padding-left: 14px;
  border-left: 1px solid #eee
}

.floating-label-form-group-with-value label {
  top: 0;
  opacity: 1
}

.floating-label-form-group-with-focus label {
  color: #18bc9c
}

form .row:first-child .floating-label-form-group {
  border-top: 1px solid #eee
}

footer {
  color: #fff
}

footer h3 {
  margin-bottom: 30px
}

footer .footer-above {
  padding-top: 50px;
  background-color: #70b9b0
}

footer .footer-col {
  margin-bottom: 50px
}

footer .footer-below {
  padding: 25px 0;
  background-color: #438496
}

.btn-social {
  display: inline-block;
  width: 50px;
  height: 50px;
  border: 2px solid #fff;
  border-radius: 100%;
  text-align: center;
  font-size: 20px;
  line-height: 45px
}

.btn.active,
.btn:active,
.btn:focus {
  outline: 0
}

.scroll-top {
  z-index: 3;
  position: fixed;
  right: 2%;
  bottom: 2%;
  width: 50px;
  height: 50px
}

.scroll-top .btn {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  font-size: 20px;
  line-height: 28px
}

.scroll-top .btn:focus {
  outline: 0
}

.portfolio-modal .modal-content {
  padding: 100px 0;
  min-height: 100%;
  border: 0;
  border-radius: 0;
  text-align: center;
  background-clip: border-box;
  -webkit-box-shadow: none;
  box-shadow: none
}

.portfolio-modal .modal-content h2 {
  margin: 0;
  font-size: 3em
}

.portfolio-modal .modal-content img {
  margin-bottom: 30px
}

.portfolio-modal .modal-content .item-details {
  margin: 30px 0
}

.portfolio-modal .close-modal {
  position: absolute;
  top: 25px;
  right: 25px;
  width: 75px;
  height: 75px;
  background-color: transparent;
  cursor: pointer
}

.portfolio-modal .close-modal:hover {
  opacity: .3
}

.portfolio-modal .close-modal .lr {
  z-index: 4;
  width: 1px;
  height: 75px;
  margin-left: 35px;
  background-color: #2c3e50;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg)
}

.portfolio-modal .close-modal .lr .rl {
  z-index: 5;
  width: 1px;
  height: 75px;
  background-color: #2c3e50;
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg)
}

.portfolio-modal .modal-backdrop {
  display: none;
  opacity: 0
}

@media(min-width:767px) {
  #portfolio .portfolio-item {
      margin: 0 0 30px
  }
}

@media(max-width:601px) {
  .responsive-circle {
      width: 40%
  }
  .btn-center {
      text-align: center
  }
}

@media(min-width:576px){
  .modal-dialog{
      max-width: none;
      margin: 0;
  }
}